      <div class="e-on"><span>厂家：</span>武汉安艺科技</div>
      <div class="e-on"><span>批号：</span>20171201</div>
      <div class="e-on"><span>规格：</span>17粒</div>
      <div class="e-on"><span>库存：</span>2件以上</div>
      <el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="fileList2" list-type="picture">
        <el-button size="small" type="primary">点击上传</el-button>
      </el-upload>
    </li>
    <li>
